.tariffs {
  padding-top: 60px;
  padding-bottom: 60px;

  &__title {
    display: flex;

    margin: 0;
    margin-bottom: 30px;
    font-weight: 600;
    font-size: 32px;
    line-height: 160%;
    @include small-mobile {
      font-size: 24px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;
    @include mobile {
      gap: 40px;
      display: flex;
      flex-direction: column;
      text-align: center;
    }
  }
  &__item {
    position: relative;
    grid-column: 6 span;
    @include flex-all-center;

    border-radius: 10px;
    padding: 15px 40px;

    @include tablet {
      grid-column-start: 1;
      grid-column-end: 13;
    }
    @include mobile {
      &:not(:last-child) {
        padding: 15px 15px;
      }
    }

    &--descr {
      font-weight: 400;
      font-size: 18px;
      line-height: 120%;
      color: var(--dark-color);
    }
    &:last-child {
      background-color: var(--accent-color);
    }

    &:not(:last-child) {
      border: 3px solid #d3d3d3;
    }
  }

  &__item--descr-link {
    color: var(--light-color);
    font-weight: 500;
  }

  &__link {
    position: absolute;
    @include flex-all-center;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }
  &__info-icon {
    position: absolute;
    right: -15px;
    @include mobile {
      right: -11px;
      top: -36px;
    }
    &--text {
      position: absolute;
      font-weight: 400;
      font-size: 14px;
      color: var(--light-color);
      transform: rotate(-15deg);
      right: -3px;
      @include mobile {
        top: -14px;
      }
    }
  }
}
