.modal {
  &__form-input {
    border: 1px solid var(--dark-color);
    border-radius: 10px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 28px;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 30px;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 15px;
  }

  &__info-text {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 30px;
  }
}
