.hero {
  // min-height: 480px;
  background-image: linear-gradient(90deg, #005fa5 32.01%, #ffffff 100%);

  padding-top: 105px;

  &-container {
    @include flex-all-sb;
    min-height: 480px;
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 50px;
    line-height: 160%;
    color: var(--light-color);
    margin-bottom: 50px;
    max-width: 690px;
    @include mobile {
      color: var(--dark-color);
      font-size: 32px;
    }
  }
  // &__title div {
  //   display: inline-block;
  // }
}
