.working {
  background-color: var(--light-color);
  padding-top: 60px;
  padding-bottom: 60px;
  @include mobile {
    padding-bottom: 0;
  }
  &__title {
    margin: 0;
    display: flex;

    margin-bottom: 30px;
    font-weight: 600;
    font-size: 32px;
    line-height: 160%;
    @include small-mobile {
      font-size: 24px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 60px;
    margin-bottom: 20px;
    @include small-tablet {
      gap: 40px;
    }
    @include mobile {
      gap: 20px;
      display: flex;
      flex-direction: column;
    }
  }

  &__item {
    grid-column: 6 span;
    padding: 5px 15px;
    @include mobile {
      grid-column: 10 span;
    }

    // @include tablet {
    //   grid-column: 6 span;
    // }

    &--number {
      border-radius: 50px;
      background-color: var(--accent-color);
      padding: 15px;
      margin-right: 10px;
    }
    &--descr {
      color: var(--dark-color);
      font-weight: 400;
      font-size: 18px;
      line-height: 120%;
    }
    &--link {
      background-color: var(--accent-color);
      @include flex-all-center;
    }
  }
  &__item:not(:last-child) {
    border-radius: 8px;
    border: 3px solid #d3d3d3;
  }
  &__link {
    @include flex-all-center;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;

    padding: 15px;
    padding-left: 20px;
    color: var(--light-color);
    font-weight: 500;
    font-size: 24px;
  }
  &__video {
    @include flex-all-center;
    flex-direction: column;
  }

  &__item1 {
    @include small-tablet {
      grid-column-start: 2;
      grid-column-end: 12;
    }
    @include mobile {
      grid-column-start: 1;
      grid-column-end: 10;
    }
    @include small-mobile {
      grid-column-start: 1;
      grid-column-end: 8;
    }
  }
  &__item2 {
    grid-column-start: 7;
    grid-column-end: 13;
    grid-row-start: 2;
    grid-row-end: 2;
    @include small-tablet {
      grid-column-start: 2;
      grid-column-end: 12;
    }
    @include mobile {
      grid-column-start: 1;
      grid-column-end: 10;
    }
    @include small-mobile {
      grid-column-start: 1;
      grid-column-end: 8;
    }
  }
  &__item3 {
    grid-column-start: 1;
    grid-column-end: 7;
    grid-row-start: 3;
    grid-row-end: 3;
    @include small-tablet {
      grid-column-start: 2;
      grid-column-end: 12;
    }
    @include mobile {
      grid-column-start: 1;
      grid-column-end: 10;
    }
    @include small-mobile {
      grid-column-start: 1;
      grid-column-end: 8;
    }
  }
  &__item4 {
    grid-column-start: 7;
    grid-column-end: 13;
    grid-row-start: 4;
    grid-row-end: 4;
    @include small-tablet {
      grid-column-start: 2;
      grid-column-end: 12;
    }
    @include mobile {
      grid-column-start: 1;
      grid-column-end: 10;
    }
    @include small-mobile {
      grid-column-start: 1;
      grid-column-end: 8;
    }
  }
  &__item5 {
    grid-column-start: 1;
    grid-column-end: 7;
    grid-row-start: 5;
    grid-row-end: 5;
    @include small-tablet {
      grid-column-start: 2;
      grid-column-end: 12;
    }
    @include mobile {
      grid-column-start: 1;
      grid-column-end: 10;
    }
    @include small-mobile {
      grid-column-start: 1;
      grid-column-end: 8;
    }
  }
  &__item6 {
    grid-column-start: 7;
    grid-column-end: 13;
    grid-row-start: 6;
    grid-row-end: 6;
    @include small-tablet {
      grid-column-start: 2;
      grid-column-end: 12;
    }
    @include mobile {
      grid-column-start: 1;
      grid-column-end: 10;
    }
    @include small-mobile {
      grid-column-start: 1;
      grid-column-end: 8;
    }
  }
}
