.benefits {
  background-color: var(--background-grey-color);
  padding-top: 60px;
  padding-bottom: 60px;

  &__title {
    margin: 0;
    display: flex;

    margin-bottom: 30px;
    font-weight: 600;
    font-size: 32px;
    line-height: 160%;

    @include small-mobile {
      text-align: start;
      font-size: 24px;
    }
  }
  &__subtitle {
    margin: 0;
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 28px;
    line-height: 160%;
    @include mobile {
      font-size: 24px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;
    margin-bottom: 30px;
    @include mobile {
      display: flex;
      flex-direction: column;
    }
  }

  &__item {
    grid-column: 4 span;
    min-height: 200px;

    @include small-tablet {
      grid-column: 6 span;
    }

    &--text {
      margin: 0;
      font-weight: 400;
      font-size: 18px;
      line-height: 160%;
      color: var(--text-color);
      @include mobile {
        max-width: 300px;
        align-self: flex-start;
      }
    }
  }
  &__btn-block {
    @include flex-h-center;
  }
}
