.about {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: var(--light-color);
  &__descr {
    margin: 0;
    margin-bottom: 60px;
    font-weight: 400;
    font-size: 24px;
    line-height: 160%;
    color: var(--dark-color);
    &-highlight {
      margin: 0;
      font-weight: 600;
      font-size: 24px;
      line-height: 160%;
      @include mobile {
        font-size: 20px;
      }
    }
    @include mobile {
      font-size: 20px;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;
    @include mobile {
      display: flex;
      flex-direction: column;
    }
  }

  &__item {
    grid-column: 4 span;
    min-height: 150px;
    display: flex;
    align-items: center;
    text-align: center;
    flex-direction: column;
    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &_digit {
    margin-bottom: 15px;
    // filter: grayscale(100%);
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--text-color);
    @include mobile {
      font-size: 16px;
    }
  }
}

.padding-top {
  padding-top: 120px;
}
