.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  border: none;
  padding: 25px 30px;
  min-width: 260px;
  font-weight: 600;
  font-size: 18px;
  line-height: 160%;
  text-align: center;
  color: var(--light-color);
  background-color: var(--accent-color);
}

.btn:focus-visible {
  outline: none;
  box-shadow: 10px 5px 5px rgb(0 0 0 / 60%);
}

.btn:hover {
  box-shadow: 10px 5px 5px rgb(0 0 0 / 60%);
}
