.contacts {
  padding-top: 30px;
  padding-bottom: 30px;

  &__title {
    display: flex;
    justify-content: start;
    margin: 0;
    margin-bottom: 30px;
    font-weight: 600;
    font-size: 32px;
    line-height: 160%;
    @include mobile {
      font-size: 24px;
    }
  }

  &__form-block {
    border-radius: 20px;
    padding: 20px;
    background-color: var(--background-grey-color);
    margin-bottom: 40px;
  }

  &__subtitle {
    margin: 0;
    font-weight: 500;
    font-size: 28px;
    margin-bottom: 40px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include mobile {
      grid-template-columns: repeat(8, 1fr);
    }
  }
  &__item {
    grid-column: 6 span;
    border-radius: 20px;
    padding: 20px;
    background-color: var(--background-grey-color);

    @include small-tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 8 span;
    }
  }

  &__item-type {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    @include mobile {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__item-type--name {
    margin: 0;
    font-weight: 500;
    font-size: 24px;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-style: 18px;
    line-height: 160%;
    color: var(--dark-color);
  }

  &__link {
    border-radius: 20px;
    padding: 10px 15px;
    border: 1px solid var(--dark-color);
    font-weight: 400;
    font-size: 18px;
    text-align: center;
    color: var(--dark-color);
  }
  &__tel {
    font-weight: 400;
    font-size: 24px;
    color: var(--nav-link-color);
    &-info {
      margin: 0;
      font-weight: 500;
      font-size: 18px;
      line-height: 160%;
    }
  }
}
