.partners {
  padding-top: 30px;
  padding-bottom: 30px;
  &__content {
    margin-bottom: 55px;
  }

  &__title {
    display: flex;
    justify-content: center;
    margin: 0;
    margin-bottom: 30px;
    font-weight: 600;
    font-size: 32px;
    line-height: 160%;
  }

  &__wrapper {
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }

  &__img {
    filter: grayscale(100%);
  }

  &__img:hover {
    filter: none;
  }

  &__nav {
    @include flex-all-sb;

    margin-bottom: 40px;
    @include mobile {
      display: none;
    }
  }

  &__scroll {
    border-radius: 40px;
    width: 360px;
    height: 5px;
    background: rgba(20, 16, 36, 0.08);
  }

  &__slider-btns {
    @include flex-v-center;
  }

  &__slider-btn {
    @include bg-cover;

    width: 40px;
    height: 40px;

    &--prev {
      background-image: url("../img/prev.svg");
    }

    &--next {
      background-image: url("../img/next.svg");
      &svg path {
        fill: var(--swiper-color);
      }
    }

    &:not(:last-child) {
      margin-right: 10px;
    }
  }
  .swiper-scrollbar-drag {
    background-color: var(--swiper-color);
  }
}
