.header {
  position: fixed;
  width: 100%;
  height: 105px;
  top: 0;
  z-index: 1000;
  background-color: var(--light-color);
  @include high-tablet {
    background-color: var(--background-grey-color);
  }

  &__container {
    @include flex-all-sb;

    padding-top: 10px;
    padding-bottom: 5px;
    z-index: 100;

    @include high-tablet {
      align-items: center;
      justify-content: initial;
    }
  }

  &_fixed {
    position: fixed;
    left: 0;
    top: 0;
    background-color: var(--background-grey-color);
  }

  &__phone {
    @include flex-all-sb;

    @include high-tablet {
      position: relative;
      margin-right: 10px;
      margin-left: auto;
      border: 1px solid var(--nav-link-color);
      border-radius: 10px;
      width: 44px;
      height: 44px;
      font-size: 0;
      background-image: url("../img/phone.svg");
      background-position: center;
      background-size: 24px 24px;
      background-repeat: no-repeat;
    }
  }
  .phone-icon {
    margin-right: 20px;
    @include high-tablet {
      display: none;
    }
  }
  &__logo {
    @include mobile {
      width: 150px;
      height: 90px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
    }
  }
}
