.globe-container,
.globe {
  width: 200px;
  height: 200px;
}
.globe-container {
  position: relative;
  display: inline-block;
  margin: 30px;
  transform: scale(1.1);
}
.globe {
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
}
.globe::before {
  content: "";
  width: 171px;
  height: 71px;
  position: absolute;
  background-image: url("../img/logoActiv.png");
  background-repeat: no-repeat;
  background-size: cover;
  top: 7%;
  // bottom: 100%;
  left: 75%;
  z-index: 100;
  @include mobile {
    top: 50%;
    transform: translate(0, -50%);
    left: 7%;
  }
}

.globe-worldmap,
.globe-worldmap-front,
.globe-worldmap-back,
.globe-sphere,
.globe-outer-shadow,
.globe-inner-shadow {
  position: absolute;
  display: block;
  margin: 0;
}
.globe-sphere,
.globe-outer-shadow,
.globe-inner-shadow {
  left: 0;
  top: 0;
  width: 200px;
  height: 200px;
  background-position: 0 0;
  background-repeat: no-repeat;
}
.globe-worldmap {
  left: 0;
  top: 0;
  width: 200px;
  height: 200px;
  overflow: hidden;
  border-radius: 50%;
  touch-action: none;
  -ms-touch-action: none;
}
.globe-worldmap-front,
.globe-worldmap-back {
  left: 0;
  top: 0;
  width: 1000px;
  height: 200px;
  overflow: visible;
  background-image: url("../img/worldmap.svg");
  background-repeat: no-repeat;
}
.globe-outer-shadow {
  left: 0;
  top: 186px;
  width: 200px;
  height: 30px;
  background-image: url("../img/outer_shadow.svg");
}
.globe-inner-shadow {
  background-image: url("../img/inner-2.svg");
}

.globe-worldmap-front {
  background-position: 0px 0px;
  animation: textureSpinreverse 8s linear infinite;
}
.globe-worldmap-back {
  background-position: 0px -220px;
  animation: textureSpin 8s linear infinite;
}

@keyframes textureSpin {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-47.5%);
  }
}
@keyframes textureSpinreverse {
  from {
    transform: translateX(-47.5%);
  }
  to {
    transform: translateX(0);
  }
}

#box {
  position: relative;
  display: inline-block;
  background: url("../img/earth.jpg");
  background-size: cover;

  border-radius: 50%;
  width: 200px;
  height: 200px;
  animation: movimiento 7s linear 0s infinite;
  box-shadow: -2px -2px 51px 4px rgba(15, 63, 194, 0.75);
  -webkit-box-shadow: -2px -2px 51px 4px rgba(15, 63, 194, 0.75);
  -moz-box-shadow: -2px -2px 51px 4px rgba(15, 63, 194, 0.75);
  /*   box-shadow:0 0 25px RGBA(255,255,255, 0.10),
             -8px -8px 15px #000 inset,
             2px 2px 25px #000 inset,
             -45px -45px 25px RGBA(0,0,0, 0.5) inset,
             25px 25px 45px RGBA(0,0,0, 0.45) inset; */

  /*   transform:rotateX(6deg) rotateY(6deg) rotateZ(6deg); */
}

#box::before {
  content: "";
  position: absolute;
  top: 7%;
  left: 65%;
  width: 171px;
  height: 71px;
  background-image: url("../img/logoActiv.png");
  background-repeat: no-repeat;
  background-size: cover;
}

@keyframes movimiento {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 355px 0;
  }
}
