.footer {
  background-image: linear-gradient(90deg, #0066ac -0.98%, #ffffff 100%);
  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    padding-top: 40px;

    margin-bottom: 30px;
    // background-image: url("../img/activA.png");
    // background-position: right center;
    // background-size: 450px;
    // background-repeat: no-repeat;
  }
  &__left,
  &__right {
    grid-column: 6 span;
  }

  &__left {
    @include small-tablet {
      margin-bottom: 20px;
    }
  }

  &__right {
    align-items: flex-end;
    padding-right: 17%;
    @include tablet {
      align-items: center;
    }
    @include small-tablet {
      align-items: flex-start;
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
    @include small-tablet {
      grid-column: 12 span;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    color: var(--light-color);
    &:not(:last-child) {
      margin-bottom: 8px;
    }
    @include mobile {
      font-weight: 500;
      font-size: 18px;
      color: var(--dark-color);
    }
  }

  &__link {
    // font-weight: 500;
    // font-size: 24px;
    // line-height: 160%;
    color: var(--light-color);
    &:focus-visible {
      color: var(--accent-color);
      outline: none;
    }
    &:hover {
      color: var(--accent-color);
    }
    @include mobile {
      font-weight: 500;
      color: var(--dark-color);
    }
  }

  &__copyright {
    padding: 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 160%;
    color: var(--light-color);
    @include mobile {
      font-weight: 500;
      color: var(--dark-color);
    }
  }
}
