.cards {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  align-self: center;
  width: 100%;
}

.card__image {
  height: 100px;
}

.card__image img {
  object-fit: cover;
}

.card__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  flex-grow: 1;
  @include mobile {
    padding: 0px;
    padding-top: 10px;
  }
}

.card__title {
  margin: 0;
  margin-bottom: 15px;
}

.card__descr {
  margin: 0;
}

.card__btn {
  margin-top: auto;
  border: none;
  padding: 10px 20px;

  background-color: var(--accent-color);
  cursor: pointer;
}

.cards__item .card {
  height: 100%;
}
