.nav {
  &__list {
    padding-top: 14px;

    @include flex-v-center;

    @include high-tablet {
      @include flex-all-center;
      flex-direction: column;
      width: 100%;
      height: 100%;
    }
    & li a {
      position: relative;

      padding: 6px 0;
      text-decoration: none;
    }
    & li a:before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      height: 2px;
      width: 0%;
      background: var(--accent-color);
      border-radius: 12px;
      transition: all 0.4s ease;
    }
    & li a:hover:before {
      width: 100%;
    }
    & li.center a:before {
      left: 50%;
      transform: translateX(-50%);
    }
    & li.upward a:before {
      width: 100%;
      bottom: -5px;
      opacity: 0;
    }
    & li.upward a:hover:before {
      bottom: 0px;
      opacity: 1;
    }
    & li.forward a:before {
      width: 100%;
      transform: scaleX(0);
      transform-origin: right;
      transition: transform 0.4s ease;
    }
    & li.forward a:hover:before {
      transform: scaleX(1);
      transform-origin: left;
    }
  }

  &__item {
    transition: transform 0.4s ease;
    &:hover {
      transform: scale(1.2);
    }
    &:not(:last-child) {
      margin-right: 40px;

      @include high-tablet {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    text-align: right;
    color: var(--nav-link-color);
    transition: transform 0.4s ease;
    &:hover {
      transform: scale(1.2);
    }

    @include high-tablet {
      font-weight: 600;
      font-size: 30px;
      line-height: 160%;
      color: var(--light-color);
    }
  }

  @include high-tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--nav-link-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
