.reportPartners {
  padding-top: 30px;
  padding-bottom: 30px;
  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 28px;
    line-height: 160%;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include mobile {
      display: flex;
      flex-direction: column;
    }
  }

  &__item {
    position: relative;
    grid-column: 3 span;
    min-height: 300px;
    @include tablet {
      grid-column: 6 span;
      grid-row-start: 1;
      grid-row-end: 1;
    }

    &-title {
      margin: 0;
      font-weight: 500;
      font-size: 24px;
      margin-bottom: 30px;
    }
  }
  &__text--text {
    font-weight: 400;
    font-size: 24px;
    line-height: 160%;
  }
  &__btns {
    position: absolute;
    bottom: -45px;
    @include mobile {
      display: none;
    }
  }

  &__item-video {
    grid-column: 6 span;

    @include tablet {
      grid-column: 12 span;
      grid-row-start: 2;
      grid-row-end: 2;
    }
  }

  &__slider-btns {
    @include flex-v-center;
  }

  &__slider-btn {
    @include bg-cover;

    width: 40px;
    height: 40px;

    &--prev {
      background-image: url("../../img/prev.svg");
    }

    &--next {
      background-image: url("../../img/next.svg");
      &svg path {
        fill: var(--swiper-color);
      }
    }

    &--prev {
      background-image: url("../../img/prev.svg");
      &svg path {
        fill: var(--swiper-color);
      }
    }

    &:not(:last-child) {
      margin-right: 10px;
    }
  }
}
